<template>
  <div class="index">
    <!--<h2>{{msg}}</h2>-->
  </div>
</template>

<script>
import { getRandomJoke, getNovelSearch, getNovelDetail } from '@/api/apiopen'

export default {
  name: 'index',
  data () {
    return {
      msg: '我是主页面',
      isCollapse: true
    }
  },
  mounted () {
    this.getJoke()
    this.searchNovel()
    this.novelInfo()
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    getJoke () {
      getRandomJoke()
        .then(res => {
          console.log(res)
        })
        .catch(res => {
          console.log(res)
        })
    },
    searchNovel () {
      getNovelSearch({
        param: {
          name: '都市奇门医圣'
        }
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    novelInfo () {
      getNovelDetail({
        param: {
          name: '都市奇门医圣'
        }
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    }
  }
}
</script>
